const db = wx.cloud.database({env: 'justfortest-gf720'});

import {
    mapState,
    mapMutations
} from 'vuex'

export default {
	data() {
		return {
			userinfo: {
				openid: "",
				username: "",
				avatarUrl: "",
				firstLogin: true,
				solvedid:[],
				questionid:[]
			}
		}
	},
	computed: {
		// 监听登录状态
		...mapState(['hasLogin'])
	},
	methods: {
		...mapMutations(['login']),
		tologin() {
			let self = this;
			uni.login({
				provider:"weixin",
				success:function(res){		
					console.log("login sucess");
					
					uni.getUserInfo({
						provider:"weixin",
						success:function(infoRes){
							self.userinfo.openid = infoRes.userInfo.openid;
							self.userinfo.username = infoRes.userInfo.nickName;
							self.userinfo.avatarUrl = infoRes.userInfo.avatarUrl;
							self.login(self.userinfo.openid);
							
							db.collection('user').where({
								_openid: self.userinfo.openid,
								firstLogin: false
							}).get({
								success: function (res) {
									self.userinfo.firstLogin === false;
									db.collection('user').add({
										data: self.userinfo,
										success: res => {
											console.log("upload success!!!");
										},
										fail: err => {
											console.error(err)
										}
									})
									console.log(res);
								}
							})
							console.log(self.userinfo.firstLogin)
							
							console.log(self.userinfo);
						},
						fail:function(infoRes){
							console.log("info failed")
						}
					})
				},
				fail:function(res){
					console.log("failed");
				}
			})
		}
	}
}
